<template>
<div class="container">
    <h1 class="pb-2">
     <icons :icon="['fas', 'star']" /> 
        FORO DE RECETAS
    </h1>
    <p> Animate y comparte tus increibles recetas con nosotros </p>

        
<!-- Formulario para añadir personas -->

        <section class="form">
            <b-form action="" class="text-center">
                <b-form-input v-model="nombre" @keyup.enter="crearPersona" type="text" class="form-control" placeholder="Nombre" ></b-form-input>
                <b-form-input v-model="tituloreceta" @keyup.enter="crearPersona" type="text" class="form-control" placeholder="Titulo de Receta"></b-form-input>
                <b-form-input v-model="ingredientes" @keyup.enter="crearPersona" type="text" class="form-control" placeholder="Ingredientes"></b-form-input>
                <b-form-input v-model="preparacion" @keyup.enter="crearPersona" type="text" class="form-control" placeholder="Pasos de preparación"></b-form-input>
                
                <!-- Botón para añadir -->
                <b-form-input @click="crearPersona" type="button" value="Añadir" class="btn btn-success"></b-form-input>
            </b-form>
        </section>
        
<!-- Tabla donde se muestran los datos -->
        <section class="data">
            <caption><h1><icons :icon="['fas', 'star']" /> 
      RECETAS PUBLICADAS</h1></caption>
            <table class="table">
                <thead>
                    <tr>
                        <th scope="col">Nombre</th>
                        <th scope="col">Titulo de la receta</th>
                        <th scope="col">Ingredientes</th>
                        <th scope="col">Preparación</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(persona, index) in personas" :key="persona.id">
                        <td>
                            <span v-if="formActualizar && idActualizar == index">
                                
                                <!-- Formulario para actualizar -->
                                <input v-model="nombreActualizar" type="text" class="form-control">
                            </span>
                            <span v-else>
                                
                                <!-- Dato nombre -->
                                {{ persona.nombre }}
                            </span>
                        </td>
                        <td>
                            <span v-if="formActualizar && idActualizar == index">
                                
                                <!-- Formulario para actualizar -->
                                <input v-model="titulorecetaActualizar" type="text" class="form-control">
                            </span>
                            <span v-else>
                                
                                <!-- Dato nombre -->
                                {{ persona.tituloreceta }}
                            </span>
                        </td>
                        <td>
                            <span v-if="formActualizar && idActualizar == index">
                                
                                <!-- Formulario para actualizar -->
                                <input v-model="ingredientesActualizar" type="text" class="form-control">
                            </span>
                            <span v-else>
                                
                                <!-- Dato nombre -->
                                {{ persona.ingredientes }}
                            </span>
                        </td>
                        <td>
                            <span v-if="formActualizar && idActualizar == index">
                                
                                <!-- Formulario para actualizar -->
                                <input v-model="preparacionActualizar" type="text" class="form-control">
                            </span>
                            <span v-else>
                                
                                <!-- Dato nombre -->
                                {{ persona.preparacion}}
                            </span>
                        </td>
                        <td>
                            
                            <!-- Botón para guardar la información actualizada -->
                            <span v-if="formActualizar && idActualizar == index">
                                <button @click="guardarActualizacion(index)" class="btn btn-success">Guardar</button>
                            </span>
                            <span v-else>
                                
                                <!-- Botón para mostrar el formulario de actualizar -->
                                <button @click="verFormActualizar(index)" class="btn btn-warning">Actualizar</button>

                                <!-- Botón para borrar -->
                                <button @click="borrarPersona(index)" class="btn btn-danger">Borrar</button>
                            </span>
                        </td>
                    </tr>
                </tbody>
            </table>
        </section>
    </div>
</template>




    <script>
    export default {
        data() {
        return {
            // Input nombre
            nombre: '',
            // Input edad
            edad: '',
            // Input reseta
            tituloreceta: '',
            ingredientes: '',
            preparacion: '',
            // Ver o no ver el formulario de actualizar
            formActualizar: false,
            // La posición de tu lista donde te gustaría actualizar 
            idActualizar: -1,
            // Input nombre dentro del formulario de actualizar
            nombreActualizar: '',
            // Input edad dentro del formulario de actualizar
            edadActualizar: '',
            // Lista de personas
            personas: [] 
         }
    },
        methods: {
            crearPersona: function () {
                
                // Anyadimos a nuestra lista
                this.personas.push({
                    id: + new Date(),
                    nombre: this.nombre,
                    tituloreceta: this.tituloreceta,
                    ingredientes: this.ingredientes,
                    preparacion: this.preparacion,
                    edad: this.edad

                });
                
                // Vaciamos el formulario de añadir
                this.nombre = '';
                this.tituloreceta = '';
                this.ingredientes = '';
                this.preparacion = '';
                this.edad = ''

            },
            verFormActualizar: function (persona_id) {
                
                // Antes de mostrar el formulario de actualizar, rellenamos sus campos
                this.idActualizar = persona_id;
                this.nombreActualizar = this.personas[persona_id].nombre;
                this.titulorecetaActualizar = this.tituloreceta[persona_id].tituloreceta;
                this.ingredientesActualizar = this.ingredientes[persona_id].ingredientes;
                this.preparacionActualizar = this.preparacion[persona_id].preparacion;
                this.edadActualizar = this.personas[persona_id].edad;

                
                // Mostramos el formulario
                this.formActualizar = true;
            },
            borrarPersona: function (persona_id) {
                
                // Borramos de la lista
                this.personas.splice(persona_id, 1);
            },
            guardarActualizacion: function (persona_id) {
                // Ocultamos nuestro formulario de actualizar
                this.formActualizar = false;

                // Actualizamos los datos
                this.personas[persona_id].nombre = this.nombreActualizar;
                this.personas[persona_id].tituloreceta = this.titulorecetaActualizar;
                this.personas[persona_id].ingredientes= this.ingredientesActualizar;
                this.personas[persona_id].preparacion = this.preparacionActualizar;
                this.personas[persona_id].edad = this.edadActualizar;

            }
        }

    }
</script>

<style>
.form-control{
  margin-top: 10px;
  margin-bottom: 10px;
}

h1{
  margin-top: 30px;
}
</style>

